.news {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__title {
    position: absolute;
    top: 0;
    left: 0;
  }

  &__items {
    position: relative;
    display: grid;
    grid-template-columns: repeat(auto-fill, 235px);
    gap: 25px;
    justify-content: space-between;
    padding-top: 55px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 235px;
    height: 325px;
  }

  &__illustration {
    display: inline-block;
    width: 100%;
    height: auto;
    object-fit: cover;
  }

  &__text {
    margin: 10px 0;
    line-height: 18px;
    text-align: center;
  }

  &__date {
    font-size: 12px;
    color: $gray;
  }

  &__btn-more {
    display: none;
  }
}

@media screen and (max-width: 1356px) {
  .news {
    &__items {
      max-height: 730px;
      overflow: hidden;

      &_open {
        max-height: initial;
      }
    }

    &__btn-more {
      display: inline-block;
      width: max-content;
      padding: 8px 25px;
      margin: 25px 0;
      font-size: 14px;
      font-weight: 500;
      line-height: 1.4em;
      color: $blue;
      background-color: transparent;
      border: 2px solid $blue;
      border-radius: 50px;
    }
  }
}

@media screen and (max-width: 805px) {
  .news {
    &__items {
      justify-content: space-evenly;
      padding-top: 75px;
    }

    &__title {
      text-align: center;
    }
  }
}
