.new-products {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 94%;
  max-width: 1440px;
  padding: 40px 0;
  margin: 0 auto;

  &__link-more {
    font-size: 18px;
    line-height: 32px;
    color: $black;
    text-decoration: underline;
  }

  &__swiper-container {
    position: relative;
    display: flex;
    width: 100%;
    overflow: hidden;
  }

  &__items {
    display: flex;
    width: 100%;
    padding: 15px 0;
  }
}
