.responses {
  display: block;
  width: 94%;
  max-width: 1160px;
  height: max-content;
  margin: 50px auto;
  background-color: #f5f7ff;

  &__wrapper {
    display: block;
    width: 80%;
    max-width: 1160px;
    height: max-content;
    padding: 50px 0;
    margin: 0 auto;
    background-color: #f5f7ff;
  }

  &__swiper {
    position: relative;
    display: flex;
    flex-direction: row;
    width: 100%;
    overflow: hidden;
  }

  &__items {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: max-content;
    padding-bottom: 60px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    align-items: end;
    width: 100%;
    height: 100%;
  }

  &__text {
    display: inline-block;
    width: 100%;
    padding-left: 66px;
    font-size: 18px;
    line-height: 27px;
    color: $black;
    background: url("../images/‘’.jpg") no-repeat left top;
    background-size: 40px auto;
  }

  &__author {
    display: inline-block;
    padding-top: 25px;
    font-size: 14px;
    line-height: 21px;
    color: $black;
  }

  &__author::before {
    content: "-";
  }

  &__btn {
    position: absolute;
    bottom: 0;
    left: 0;
    display: inline-block;
    padding: 8px 26px;
    font-size: 14px;
    font-weight: 600;
    line-height: 21px;
    color: $blue;
    border: 2px solid #0156ff;
    border-radius: 50px;
  }
}

@media screen and (max-width: 480px) {
  .responses {
    &__items {
      padding-bottom: 120px;
    }

    &__text {
      padding-top: 25px;
      padding-left: 0;
    }

    &__pagination {
      bottom: 70px;
    }
  }
}
