.contacts {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  max-width: 262px;
  max-height: 232px;
  background-color: $white;
  border: 1px solid rgba(0, 0, 0, 0.2);

  &__work-schedule {
    display: flex;
    flex-direction: column;
    padding: 10px 15px;
    padding-left: 38px;
    background-image: url("../images/header/time.png");
    background-repeat: no-repeat;
    background-position: left 10px;
    background-size: 35px;
  }

  &__text {
    padding: 2px 0;
    font-family: "Poppins", sans-serif;
    font-size: 12px;
    font-style: normal;
    font-weight: normal;
    line-height: 140%;
    color: #000;

    &_blue {
      color: blue;
    }
  }

  &__day-week {
    padding: 3px 0;
    font-size: 13px;
    font-style: normal;
    font-weight: 600;
    line-height: 14px;
    color: $gray;

    &_black {
      color: #000;
    }
  }

  &__addres {
    display: flex;
    padding: 10px 15px;
    padding-left: 38px;
    background-image: url("../images/header/location.png");
    background-repeat: no-repeat;
    background-position: left 10px;
    background-size: 35px;
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }

  &__links {
    display: grid;
    grid-template-columns: auto 1fr;
    row-gap: 2px;
    column-gap: 3px;
    padding: 10px 15px;
  }
}
