.hot-products {
  position: relative;
  display: flex;
  width: 94%;
  max-width: 1440px;
  height: max-content;
  margin: 0 auto;
  overflow: hidden;

  &__items {
    display: flex;
    width: 100%;
    height: max-content;
  }

  &__item {
    display: flex;
    flex-shrink: 0;
    height: max-content;
  }

  &__illustration {
    display: flex;
    width: 100%;
    height: auto;
    object-fit: contain;
  }
}

@media screen and (max-width: 1100px) {
  .hot-products {
    margin: 20px 3%;
  }
}
