}

.swiper-button-prev {
  right: auto;
  left: 0;
  background-image: url("../images/prev-arrow.svg");
}

.swiper-button-next,
.swiper-button-prev {
  display: inline-block;
  width: 36px;
  height: 48px;
  color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.swiper-pagination {
