.burger {
  position: fixed;
  top: 50px;
  left: 3%;
  z-index: 20;
  display: none;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  cursor: pointer;
  background-color: #0156ff;
  border-radius: 50%;
}

.burger__middle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 4px;
  margin: 14px 0;
  background-color: #fff;
  background-size: 20px 10px;
  transition: 0.3s;
}

.burger__top {
  position: absolute;
  top: 12.8px;
  left: 8px;
  z-index: 100;
  display: inline-block;
  width: 32px;
  height: 4px;
  background-color: #fff;
  transition: top 0.3s, transform 0.3s;
}

.burger__bottom {
  position: absolute;
  bottom: 12.8px;
  left: 8px;
  z-index: 100;
  display: inline-block;
  width: 32px;
  height: 4px;
  background-color: #fff;
  transition: top 0.3s, transform 0.3s;
}

.burger_open > .burger__middle {
  background-color: transparent;
  transition: 0.3s;
}

.burger_open > .burger__top {
  top: 21.33333px;
  transition: top 0.3s, transform 0.3s;
  transform: rotate(45deg);
}

.burger_open > .burger__bottom {
  top: 21.33333px;
  transition: top 0.3s, transform 0.3s;
  transform: rotate(-45deg);
}

@media screen and (max-width: 1100px) {
  .burger {
    display: flex;
  }
}

