.news {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.news__title {
  position: absolute;
  top: 0;
  left: 0;
}

.news__items {
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fill, 235px);
  gap: 25px;
  justify-content: space-between;
  padding-top: 55px;
}

.news__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 235px;
  height: 325px;
}

.news__illustration {
  display: inline-block;
  width: 100%;
  height: auto;
  object-fit: cover;
}

.news__text {
  margin: 10px 0;
  line-height: 18px;
  text-align: center;
}

.news__date {
  font-size: 12px;
  color: #999;
}

.news__btn-more {
  display: none;
}

@media screen and (max-width: 1356px) {
  .news__items {
    max-height: 730px;
    overflow: hidden;
  }
  .news__items_open {
    max-height: initial;
  }
  .news__btn-more {
    display: inline-block;
    width: max-content;
    padding: 8px 25px;
    margin: 25px 0;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.4em;
    color: #0156ff;
    background-color: transparent;
    border: 2px solid #0156ff;
    border-radius: 50px;
  }
}

@media screen and (max-width: 805px) {
  .news__items {
    justify-content: space-evenly;
    padding-top: 75px;
  }
  .news__title {
    text-align: center;
  }
}

