.brands {
  margin: 35px 0;

  &__swiper {
    position: relative;
    display: flex;
    width: 100%;
    overflow: hidden;
  }

  &__items {
    display: flex;
    width: 100%;
  }

  &__link {
    display: flex;
    flex-shrink: 0;
    width: 205.6px;
    height: 150px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 153px 80px;
  }

  &__link:nth-child(7n + 1) {
    background-image: url("../images/brands/image.jpg");
  }

  &__link:nth-child(7n + 2) {
    background-image: url("../images/brands/image2.jpg");
  }

  &__link:nth-child(7n + 3) {
    background-image: url("../images/brands/image3.jpg");
  }

  &__link:nth-child(7n + 4) {
    background-image: url("../images/brands/image4.jpg");
  }

  &__link:nth-child(7n + 5) {
    background-image: url("../images/brands/image5.jpg");
  }

  &__link:nth-child(7n + 6) {
    background-image: url("../images/brands/image6.jpg");
  }

  &__link:nth-child(7n + 7) {
    background-image: url("../images/brands/image7.jpg");
  }
}
