.footer {
  padding-top: 50px;
  color: $white;
  background-color: $black;

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__subscription {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 25px;
  }

  &__subscription-text {
    display: flex;
    flex-direction: column;
    padding-bottom: 25px;
    margin-right: 25px;
  }

  &__subscription-title {
    font-size: 38px;
    font-weight: 500;
    line-height: 132.5%;
  }

  &__subscription-subtitle {
    font-size: 16px;
    line-height: 132.5%;
  }

  &__form {
    display: flex;
    flex-shrink: 0;
    padding-bottom: 25px;
  }

  &__input-email {
    padding: 15px;
    margin-right: 25px;
    font-size: 14px;
    line-height: 20px;
    color: $white;
    background: $black;
    border: 2px solid $white;
    border-radius: 4px;
  }

  &__subscribe-btn {
    padding: 15px 25px;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    color: $white;
    background-color: $blue;
    border-radius: 50px;
  }

  &__links-items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    width: 100%;
    margin-bottom: 35px;
  }

  &__links-category {
    display: flex;
    flex-direction: column;
    width: max-content;
    margin-right: 25px;
    margin-bottom: 25px;
  }

  &__title-category {
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: 500;
    line-height: 1.4em;
    cursor: pointer;
  }

  &__links-item {
    font-size: 14px;
    line-height: 1.4em;
    color: $gray;
  }

  &__link,
  &__text {
    display: flex;
    width: max-content;
    max-width: 90vw;
    font-size: 16px;
    line-height: 32px;
    color: $white;
    opacity: 0.7;
    transition: opacity 0.3s;

    &_blue {
      color: $blue;
    }
  }

  &__link:hover {
    opacity: 1;
    transition: opacity 0.3s;
  }

  &__bottom {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    min-height: 60px;
    border-top: 1px solid rgba(255, 255, 255, 0.555);
  }

  &__social {
    display: flex;
    width: max-content;
    margin: 15px 0;
    margin-right: 25px;
  }

  &__link-social {
    display: inline-block;
    width: 32px;
    height: 32px;
  }

  &__link-social:not(:last-child) {
    margin-right: 15px;
  }

  &__social-icon {
    display: inline-block;
    width: 100%;
    height: 100%;
    fill: $white;
    transition: fill 0.3s;
  }

  &__social-icon:hover {
    fill: $blue;
    transition: fill 0.3s;
  }

  &__payment-systems {
    display: flex;
    justify-content: space-between;
    width: max-content;
    height: max-content;
    margin: 15px 0;
    margin-right: 25px;
  }

  &__payment-link {
    display: inline-block;
    width: 44px;
    height: 32px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &__payment-link:not(:last-child) {
    margin-right: 15px;
  }

  &__payment-link:nth-child(1) {
    background-image: url("../images/footer/payment1.png");
  }

  &__payment-link:nth-child(2) {
    background-image: url("../images/footer/payment2.png");
  }

  &__payment-link:nth-child(3) {
    background-image: url("../images/footer/payment3.png");
  }

  &__payment-link:nth-child(4) {
    background-image: url("../images/footer/payment4.png");
  }

  &__payment-link:nth-child(5) {
    background-image: url("../images/footer/payment5.png");
  }

  &__right {
    margin: 15px 0;
    font-size: 12px;
    font-weight: 500;
    line-height: 132.5%;
    color: $white;
    opacity: 0.6;
  }
}

@media screen and (max-width: 630px) {
  .footer {
    &__bottom {
      justify-content: space-evenly;
    }
  }
}

@media screen and (max-width: 530px) {
  .footer {
    &__form {
      flex-direction: column;
      width: 100%;
    }

    &__input-email {
      width: 100%;
      margin-bottom: 15px;
    }

    &__links-items {
      flex-direction: column;
      justify-content: start;
    }

    &__links-list {
      height: 0;
      overflow: hidden;
      transition: height 0.3s;

      &_active {
        height: max-content;
        transition: height 0.3s;
      }
    }

    &__bottom {
      flex-direction: column;
    }

    &__social,
    &__payment-systems {
      margin-right: 0;
    }
  }
}
