.category-product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 242px;
  height: 400px;
  padding: 175px 20px 32px 20px;

  &__title {
    font-size: 22px;
    font-weight: 600;
    line-height: 130%;
    color: $white;
    text-align: center;
  }

  &__link {
    font-size: 13px;
    line-height: 115%;
    color: $white;
    text-decoration: underline;
  }
}

@media screen and (max-width: 707px) {
  .category-product {
    width: 100%;
    margin-bottom: 15px;
  }
}
