.products {
  margin-bottom: 35px;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
  }

  &__swiper {
    position: relative;
    display: flex;
    width: calc(100% - 242px);
    overflow: hidden;
  }

  &__items {
    display: flex;
    width: 100%;
  }
}

@media screen and (max-width: 707px) {
  .products {
    &__wrapper {
      flex-direction: column;
    }

    &__swiper {
      width: 100%;
    }
  }
}
