.header {
  &__top-bar {
    display: flex;
    width: 100%;
    min-height: 44px;
    background-color: #000;
  }

  &__wrap-bar {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__btn-modal {
    display: inline-block;
    width: max-content;
    height: max-content;
    padding-right: 20px;
    font-size: 12px;
    font-weight: 600;
    line-height: 27px;
    color: $gray;
    background-color: transparent;
    background-image: url("../images/header/arrow.png");
    background-repeat: no-repeat;
    background-position: right 6px;
    background-size: 16px 15px;

    &_bold {
      color: $white;
    }
  }

  &__visit {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: max-content;
    height: 100%;
  }

  &__visit-text {
    margin-right: 8px;
    font-size: 12px;
    font-weight: 600;
    line-height: 18px;
    color: $gray;
  }

  &__visit-link {
    font-size: 12px;
    font-weight: 600;
    line-height: 18px;
    color: $white;
    text-decoration: underline;
  }

  &__number-link {
    margin-right: 15px;
    font-size: 12px;
    font-weight: 600;
    line-height: 18px;
    color: $white;
  }

  &__call-us {
    display: flex;
    align-items: center;
  }

  &__social-links {
    display: flex;
    width: max-content;
    height: max-content;
  }

  &__social-link {
    display: inline-block;
    width: 20px;
    height: 20px;
  }

  &__social-link:not(:last-child) {
    margin-right: 15px;
  }

  &__social-icon {
    width: 100%;
    height: 100%;
    fill: $white;

    &:hover {
      fill: $blue;
    }
  }

  &__nav-menu {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 94%;
    max-width: 1440px;
    min-height: 90px;
    margin: 0 auto;
  }

  &__menu-links {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__menu-link {
    display: inline-block;
    padding: 8px 25px;
    font-size: 14px;
    font-weight: 600;
    line-height: 21px;
    color: $black;
    border: 2px solid transparent;
    border-radius: 50px;
    transition: color 0.3s, border 0.3s;
  }

  &__menu-link:hover {
    color: $blue;
    border: 2px solid $blue;
    border-radius: 50px;
    transition: color 0.3s, border 0.3s;
  }

  &__logo {
    display: inline-block;
    width: 85px;
    height: 70px;
    background-image: url("../images/Logo.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  &__main-btn {
    display: flex;
  }

  &__cart {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    margin-right: 15px;
    background-color: transparent;
  }

  &__cart-icon {
    display: inline-block;
    width: 25px;
    height: 25px;
    transition: fill 0.3s;
  }

  &__cart:hover .header__cart-icon {
    fill: $blue;
    transition: fill 0.3s;
  }

  &__account {
    position: relative;
    display: inline-block;
    width: 36px;
    height: 36px;
    background-color: transparent;
    background-image: url("../images/header/userPic.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  &__btn-event {
    position: absolute;
    top: -8px;
    right: -8px;
    display: inline-block;
    width: 16px;
    height: 16px;
    font-size: 10px;
    font-weight: 600;
    line-height: 15px;
    color: $white;
    background-color: $blue;
    border-radius: 50%;
  }
}

@media screen and (max-width: 1340px) {
  .header {
    &__menu-link {
      padding: 8px 12px;
    }
  }
}

@media screen and (max-width: 1100px) {
  .header {
    &__wrap-bar {
      padding-left: 110px;
    }

    &__call-us,
    &__visit-text,
    &__menu-links {
      display: none;
    }

    &__nav-menu {
      position: relative;
      justify-content: end;
      width: 100%;
      min-height: 64px;
      padding: 0 3%;
      background-color: $blue;
    }

    &__logo {
      position: absolute;
      top: -35px;
      left: 5%;
      width: 64px;
      height: 50px;
      background-image: url("../images/logo-middle.png");
    }

    &__cart-icon {
      fill: $white;
      transition: fill 0.3s;
    }

    &__cart:hover .header__cart-icon {
      fill: $black;
      transition: fill 0.3s;
    }

    &__btn-event {
      color: $blue;
      background-color: #fff;
    }
  }
}

@media screen and (max-width: 570px) {
  .header {
    &__wrap-bar {
      padding-left: 75px;
    }

    &__logo {
      left: 3%;
    }
  }
}

@media screen and (max-width: 366px) {
  .header {
    &__visit-link {
      display: none;
    }
  }
}
