.zip-banner {
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    min-height: 70px;
    padding: 15px;
    background: #f5f7ff;
  }

  &__text {
    display: inline-block;
    width: max-content;
    padding-left: 100px;
    margin-right: 10px;
    font-size: 18px;
    line-height: 120.5%;
    color: #272560;
    background: url("../images/zip-banner.png") no-repeat left center;
  }

  &__link {
    font-size: 14px;
    line-height: 120.5%;
    color: #272560;
    text-decoration: underline;
  }
}
