$blue: #0156ff;
$black: #000;
$white: #fff;
$gray: #999;

// common styles
body {
  font-family: "Poppins", sans-serif;
}

.container {
  width: 94%;
  max-width: 1440px;
  margin: 0 auto;
}

.swiper-button-next {
  right: 0;
  left: auto;
  background-image: url("../images/next-arrow.svg");
}

.swiper-button-prev {
  right: auto;
  left: 0;
  background-image: url("../images/prev-arrow.svg");
}

.swiper-button-next,
.swiper-button-prev {
  display: inline-block;
  width: 36px;
  height: 48px;
  color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.swiper-pagination {
  text-align: right;
}

.swiper-pagination-bullet {
  width: 14px;
  height: 14px;
}

.title-section {
  font-size: 22px;
  font-weight: 600;
  line-height: 33px;
}

@media screen and (max-width: 480px) {
  .swiper-pagination {
    text-align: center;
  }

  .swiper-container-horizontal > .swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
    bottom: 70px;
  }
}
