.card {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  width: 242px;
  max-height: 400px;
  padding: 25px;

  &__illustration {
    display: inline-block;
    width: auto;
    height: 150px;
    margin: 0 auto;
    object-fit: contain;
  }

  &__rating {
    display: flex;
    align-items: center;
    height: 30px;
    margin-bottom: 5px;
  }

  &__stars {
    display: flex;
    width: 80px;
    height: 22px;
    margin-right: 10px;
  }

  &__star {
    display: inline-block;
    width: 20%;
    height: 100%;
    font-size: 18px;
  }

  &__star::before {
    color: $gray;
    content: "★";
  }

  &__star_fill::before {
    color: #e9a426;
  }

  &__reviews {
    font-size: 12px;
    line-height: 22px;
    color: $gray;
  }

  &__title {
    margin-bottom: 5px;
    font-size: 14px;
    line-height: 22px;
    color: $black;
  }

  &__old-price {
    font-size: 14px;
    line-height: 140%;
    text-decoration-line: line-through;
    color: #666;
  }

  &__current-price {
    margin-bottom: 5px;
    font-size: 20px;
    font-weight: 500;
    line-height: 140%;
    color: $black;
  }

  &__old-price::before,
  &__current-price::before {
    content: "$";
  }

  &__btn-cart {
    display: flex;
    align-items: center;
    justify-content: center;
    width: max-content;
    padding: 5px 24px;
    margin: 0 auto;
    font-size: 14px;
    font-weight: 600;
    line-height: 21px;
    color: $blue;
    background-color: transparent;
    border: 2px solid $blue;
    border-radius: 50px;
  }

  &__cart-icon {
    display: inline-block;
    width: 26px;
    height: 24px;
    margin-right: 10px;
    fill: $blue;
  }

  &__favorites {
    position: absolute;
    top: 16px;
    right: 12px;
    left: auto;
    width: 30px;
    height: 30px;
    background-image: url("../images/favorites.svg");
  }

  &__specifications {
    position: absolute;
    top: 51px;
    right: 12px;
    left: auto;
    width: 30px;
    height: 30px;
    background-image: url("../images/specifications.svg");
  }

  &__placeholder {
    position: absolute;
    top: 0;
    left: 25px;
    display: flex;
    align-items: center;
    width: max-content;
    height: 26px;
    padding-left: 12px;
    font-size: 10px;
    line-height: 210%;
    color: #78a962;
    background-image: url("../images/placeholder1-\ icon.png");
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 10px;
  }

  &__placeholder-two {
    position: absolute;
    top: 0;
    left: 25px;
    display: flex;
    align-items: center;
    width: max-content;
    height: 26px;
    padding-left: 12px;
    font-size: 10px;
    line-height: 210%;
    color: #c94d3f;
    background-image: url("../images/plch2-icon.png");
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 10px;
  }
}
