.conditions {
  margin: 50px 0;

  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 94%;
    max-width: 1055px;
    margin: 0 auto;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 265px;
    padding: 0 15px;
    padding-top: 90px;
    text-align: center;
    background-repeat: no-repeat;
    background-position: center top;
    background-size: 65px;
  }

  &__item:nth-child(1) {
    background-image: url("../images/conditions/Support.png");
  }

  &__item:nth-child(2) {
    background-image: url("../images/conditions/Account.png");
  }

  &__item:nth-child(3) {
    background-image: url("../images/conditions/Saving.png");
  }

  &__title {
    margin-bottom: 15px;
    font-size: 18px;
    font-weight: 600;
    line-height: 132.5%;
    color: $black;
  }

  &__text {
    font-size: 14px;
    line-height: 140%;
    color: #000;
    opacity: 0.7;
  }
}

@media screen and (max-width: 850px) {
  .conditions {
    &__items {
      justify-content: space-evenly;
    }

    &__item:not(:last-child) {
      padding-top: 75px;
      margin-bottom: 35px;
    }
  }
}