.search-form {
  &__input {
    display: none;
  }

  &__btn-reset {
    display: none;
  }

  &__btn-search {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    margin-right: 15px;
    background-color: transparent;
  }

  &__btn-icon {
    display: inline-block;
    width: 19px;
    height: 19px;
    transition: fill 0.3s;
  }

  &__btn-search:hover &__btn-icon {
    fill: $blue;
    transition: fill 0.3s;
  }

  &__input_active {
    position: absolute;
    top: 18px;
    left: 115px;
    display: inline-block;
    width: calc(100% - 115px - 166px + 25px);
    height: 60px;
    padding: 23px 50px 23px 30px;
    font-size: 14px;
    line-height: 21px;
    color: #8c8c8c;
    background: #ebebeb;
    border-radius: 31px;
  }

  &__btn-reset_active {
    position: absolute;
    right: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 42px;
    height: 42px;
    background-color: transparent;
    border: 1px solid transparent;
    border-radius: 50%;
    transition: border 0.3s;
  }

  &__btn-reset_active:hover {
    border: 1px solid $gray;
    transition: border 0.3s;
  }

  &__reset-icon {
    display: inline-block;
    width: 18px;
    height: 18px;
    fill: #8c8c8c;
  }

  &__btn-reset_active:hover &__reset-icon {
    fill: rgb(189, 11, 11);
  }
}

@media screen and (max-width: 1100px) {
  .search-form {
    &__btn-icon {
      fill: $white;
      transition: fill 0.3s;
    }

    &__btn-search:hover &__btn-icon {
      fill: $black;
      transition: fill 0.3s;
    }

    &__input {
      position: absolute;
      top: 7px;
      left: 115px;
      display: inline-block;
      width: calc(100% - 115px - 166px);
      height: 38px;
      padding: 23px 50px 23px 30px;
      font-size: 14px;
      line-height: 21px;
      color: #8c8c8c;
      background: #ebebeb;
      border-radius: 31px;
    }

    &__btn-reset {
      position: absolute;
      right: 175px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 32px;
      height: 32px;
      background-color: transparent;
      border: 1px solid transparent;
      border-radius: 50%;
      transition: border 0.3s;
    }

    &__btn-reset:hover {
      border: 1px solid $gray;
      transition: border 0.3s;
    }
  }
}

@media screen and (max-width: 570px) {
  .search-form {
    &__input {
      top: 9px;
      left: 75px;
      width: calc(100% - 115px - 105px);
      padding: 20px;
      font-size: 12px;
      line-height: 38px;
    }

    &__btn-reset {
      right: 152px;
      width: 28px;
      height: 28px;
    }
  }
}

@media screen and (max-width: 366px) {
  .search-form {
    &__input,
    &__btn-reset {
      display: none;
    }
  }
}
