.sub-category {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin-bottom: 20px;

  &__item {
    margin: 5px 0;
    margin-right: 25px;
  }

  &__button {
    display: inline-block;
    width: max-content;
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
    color: $gray;
    border-bottom: 2px solid transparent;

    &_active {
      color: $black;
      border-bottom: 2px solid $blue;
    }
  }
}
